/* basic layout design */
* {
  margin: 0;
  padding: 0;
}
.main-Container {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.input-area-dark {
  background: #e0f9b5;
  box-sizing: border-box;
  resize: none;
  padding: 10px 20px;
  margin-top: 20px;
  width: 85vw;
  height: 45vh;
  font-size: 20px;
}

.input-area-light {
  background-color: #140d35;
  box-sizing: border-box;
  resize: none;
  padding: 10px 20px;
  margin-top: 20px;
  width: 85vw;
  height: 45vh;
  color: #fa58b6;
  border: 2px solid grey;
  font-size: 20px;
}
.input-area::-webkit-scrollbar {
  display: none;
}
.textarea-img {
  width: 35px;
  background: #e0f9b5;
  position: absolute;
  margin-top: 25px;
  margin-left: -40px;
}
.textarea-img2 {
  width: 30px;
  position: absolute;
  background: #e0f9b5;
  margin-top: 26px;
  margin-left: -80px;
}
.textarea-img:hover {
  opacity: 0.7;
  cursor: pointer;
}
.textarea-img2:hover {
  opacity: 0.7;
  cursor: pointer;
}
.dark {
  background-color: #1a1a40;
  color: #fa58b6;
}
.lightLite {
  background-color: #046d5f;
  border: none;
  color: #e0f9b5;
  padding: 5px 10px;
  font-weight: 600;
  border-radius: 5px;
}
.darkLite {
  background-color: #ccee0a;
  border: none;
  color: #0c0642;
  padding: 5px 10px;
  font-weight: 600;
  border-radius: 5px;
}
@media screen and (max-width: 600px) {
  .selfish {
    display: none;
  }
  .input-area {
    font-size: 14px;
    width: 95vw;
    height: 35vh;
    padding: 10px 10px;
  }
  .textarea-img {
    width: 30px;
  }
  .textarea-img2 {
    width: 25px;
    margin-left: -70px;
  }
}
